import Vue from 'vue'
import VueRouter from 'vue-router'
import index from "@/moudle/apps/index";


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component : index,
    redirect: "/index",
    children:[
      {
        path:'/index',
        component: () => import('../moudle/apps/dynamic-thread-pool-list/index')
      },
    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
